body {
  background-color: #f0f0f0;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

h1 {
  text-align: center;
  padding: 1rem;
  border-radius: 10px 10px 30px rgba(0, 0, 0, 0.1);
  background-color: #fff;
}

.card {
  margin: 1rem;
  padding: 1rem;
  border-radius: 10px 10px 30px rgba(0, 0, 0, 0.1);
  background-color: #fff;
  display: flex;
  justify-content: center;
  width: 40rem;
  margin: 3rem auto;

  .imgs {
    height: 200px;
    margin: 1rem;

    img {
      height: 160px;
    }
  }

  .infos {
    margin: 1rem;
  }
}

.btns {
  display: flex;
}
#btn {
  text-align: center;
  margin: 0 auto;
  cursor: pointer;
}
